import React from 'react';
import { Box, Select, FormField, TextInput, Button } from 'grommet';
import Layout from '../components/layout';

let people = {};
class Index extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      people
    };
  }

  onAddName = (event, name) => {
    console.log('name added');
    this.setState({
      people: people.push({key: name, value: 0})
    });
  } 

  render() {
    return (
      <Layout>
        <Box direction='column' align='center'>
          <Box direction='row'>
            <FormField label='Name'>
              <TextInput 
                id='item' 
                name='item' 
                onInput={this.onAddName} />
            </FormField>
            <Box align='center'>
            <br />
              <Button 
                    label='Add name'
                    onClick={() => {this.onAddName}}
              />
            </Box>
          </Box>
          <br />
          <br />
          <Box direction='row'>
            Add 1 to person:&nbsp;&nbsp;&nbsp;
              <Select 
              />
          </Box>
          <br />
          <br />
        </Box>
      </Layout>
    );
  }
}

export default Index;



